import React, { useState } from "react";
import {
  Container,
  Row,
  Col,
  Card,
  Form,
  Button,
  Alert,
} from "react-bootstrap";
import { Link, useNavigate } from "react-router-dom";
import { useAuth } from "../contexts/AuthContext";
import logoNexoSQL from "../assets/logo_nexosql.svg";
import loginImage from "../assets/Imagen_Login-Register.png";
import "../styles/AuthPages.css";

// Add Font Awesome CDN for icons
if (!document.querySelector('link[href*="font-awesome"]')) {
  const fontAwesome = document.createElement('link');
  fontAwesome.rel = 'stylesheet';
  fontAwesome.href = 'https://cdnjs.cloudflare.com/ajax/libs/font-awesome/6.0.0/css/all.min.css';
  document.head.appendChild(fontAwesome);
}

const Register = () => {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");
  const [error, setError] = useState("");
  const [loading, setLoading] = useState(false);

  const { signup, loginWithGoogle } = useAuth();
  const navigate = useNavigate();

  const validateForm = () => {
    if (!email || !password || !confirmPassword) {
      setError("Por favor complete todos los campos");
      return false;
    }

    if (password !== confirmPassword) {
      setError("Las contraseas no coinciden");
      return false;
    }

    if (password.length < 6) {
      setError("La contrasea debe tener al menos 6 caracteres");
      return false;
    }

    return true;
  };

  const handleEmailRegister = async (e) => {
    e.preventDefault();

    if (!validateForm()) return;

    try {
      setError("");
      setLoading(true);

      const result = await signup(email, password);

      if (result.error) {
        throw new Error(result.error);
      }

      // Registration successful, navigate to welcome page
      navigate("/welcome");
    } catch (error) {
      setError("Error al registrarse: " + error.message);
    } finally {
      setLoading(false);
    }
  };

  const handleGoogleRegister = async () => {
    try {
      setError("");
      setLoading(true);

      const result = await loginWithGoogle();

      if (result.error) {
        throw new Error(result.error);
      }

      // Registration successful, navigate to welcome page
      navigate("/welcome");
    } catch (error) {
      setError("Error al registrarse con Google: " + error.message);
    } finally {
      setLoading(false);
    }
  };

  return (
    <Container fluid className="auth-container">
      <Row className="h-100 g-0">
        {/* Left Side - Form */}
        <Col lg={6} className="auth-form-section-dark">
          <div className="text-center auth-logo-section">
            <div className="d-flex align-items-center justify-content-center mb-4">
              <img
                src={logoNexoSQL}
                alt="NexoSQL Logo"
                className="auth-logo auth-logo-animated"
              />
              <h1 className="mb-0 auth-title-dark auth-title-animated">NexoSQL</h1>
            </div>
          </div>
          <div className="auth-welcome-section">
            <h2 className="text-center auth-subtitle-dark"> Crear Cuenta</h2>
            <p className="text-center auth-description">nete y comienza a gestionar tus bases de datos</p>
          </div>

          {error && <Alert variant="danger" className="auth-alert-dark">{error}</Alert>}

          <Form onSubmit={handleEmailRegister} className="auth-form-dark">
            <Form.Group className="mb-3" controlId="email">
              <Form.Label className="auth-label-dark">
                <i className="fas fa-envelope me-2"></i>
                Correo Electrnico
              </Form.Label>
              <Form.Control
                type="email"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                placeholder=" Ingrese su correo electrnico"
                required
                className="auth-form-control-dark"
              />
            </Form.Group>

            <Form.Group className="mb-3" controlId="password">
              <Form.Label className="auth-label-dark">
                <i className="fas fa-lock me-2"></i>
                Contrasea
              </Form.Label>
              <Form.Control
                type="password"
                value={password}
                onChange={(e) => setPassword(e.target.value)}
                placeholder=" Ingrese su contrasea"
                required
                className="auth-form-control-dark"
              />
              <Form.Text className="auth-help-text">
                <i className="fas fa-info-circle me-1"></i>
                La contrasea debe tener al menos 6 caracteres.
              </Form.Text>
            </Form.Group>

            <Form.Group className="mb-4" controlId="confirmPassword">
              <Form.Label className="auth-label-dark">
                <i className="fas fa-check-circle me-2"></i>
                Confirmar Contrasea
              </Form.Label>
              <Form.Control
                type="password"
                value={confirmPassword}
                onChange={(e) => setConfirmPassword(e.target.value)}
                placeholder=" Confirme su contrasea"
                required
                className="auth-form-control-dark"
              />
            </Form.Group>

            <div className="d-grid gap-3">
              <Button 
                variant="primary" 
                type="submit" 
                disabled={loading}
                size="lg"
                className="auth-btn-primary-dark"
              >
                {loading ? (
                  <>
                    <i className="fas fa-spinner fa-spin me-2"></i>
                    Cargando...
                  </>
                ) : (
                  <>
                    <i className="fas fa-user-plus me-2"></i>
                    Crear Cuenta
                  </>
                )}
              </Button>

              <div className="text-center my-3 auth-divider-dark">
                <span>O contina con</span>
              </div>

              <Button
                variant="outline-light"
                onClick={handleGoogleRegister}
                disabled={loading}
                type="button"
                size="lg"
                className="auth-btn-google-dark"
              >
                <i className="fab fa-google me-2"></i>
                Google
              </Button>
            </div>
          </Form>

          <div className="text-center auth-link-section-dark">
            <span>Ya tienes una cuenta? </span>
            <Link to="/login" className="auth-link-dark">
              <i className="fas fa-sign-in-alt me-1"></i>
              Inicia sesin aqu
            </Link>
          </div>
        </Col>

        {/* Right Side - Image */}
        <Col lg={6} className="d-none d-lg-block">
          <div className="auth-image-section" style={{ backgroundImage: `url(${loginImage})` }}>
          </div>
        </Col>
      </Row>
    </Container>
  );
};

export default Register;
